@media screen and (max-width: 959px) {
    @keyframes avatarWrapperInBottom {

        from {
            transform: translate(0, 100%);
        }
        50% {
            transform: translate(0, -10%);
        }
        to {
            transform: translate(0, 0);
        }
    }
}

@media screen and (min-width: 959px) {
    @keyframes avatarWrapperInBottom {
        from {
            transform: translate(0, 100%);
        }
        50% {
            transform: translate(0, -5%);
        }
        to {
            transform: translate(0, 0);
        }
    }
}

.avatarWrapperAnim {
    animation-name: avatarWrapperInBottom;
}

@media screen and (max-width: 959px) {
    @keyframes avatarInBottom {
        from {
            box-shadow: 0 0 rgba(0, 0, 0, .78);
            opacity: 0;
            transform: rotate(50deg);
        }
        to {
            opacity: 1;
            transform: rotate(0deg);
            boxShadow: 0 10px 10px -8px rgba(0, 0, 0, .78);
        }
    }
}

@media screen and (min-width: 959px) {
    @keyframes avatarInBottom {
        from {
            boxShadow: 0 0 rgba(0, 0, 0, .78);
            opacity: 0;
            transform: rotate(50deg);
        }
        to {
            opacity: 1;
            transform: rotate(0deg);
            boxShadow: 0 10px 10px -8px rgba(0, 0, 0, .78);
        }
    }
}

.avatarAnim {
    animation-name: avatarInBottom;
}

@media screen and (max-width: 959px) {
    @keyframes headerInfoAvatarPadding {
        50% {
            padding: 30px;
        }
        to {
            padding: 15px 20px;
        }
    }
}

.headerInfoAvatarAnim {
    animation-name: headerInfoAvatarPadding;
}
