

@keyframes abtInfoFadeInUp {
    from {
        opacity: 0;
        transform: translateY(20px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

.abtInfoFadeInUpAnim {
    animation-name: abtInfoFadeInUp;
}

@keyframes abtDetailFadeInLeft {
    from {
        opacity: 0;
        transform: translateX(30px);
    }
    to {
        opacity: 1;
        transform: translateX(0);
    }
}

.abtDetailFadeInLeftAnim {
    animation-name: abtDetailFadeInLeft;
}
